export default {
  data () {
    return {
      goodsList: []
    }
  },
  onShow () {
    // 从内存里面获取cart
    let cart = this.$store.getters.getCart
    this.getGoodsList(cart)
    // 设置购物车角标
    wx.setTabBarBadge({
      index: 2, // tabBar的哪一项，从左边算起,
      text: this.badgeNum.toString() // 显示的文本，超过 3 个字符则显示成“…”,
    })
  },
  onHide () {
    this.$store.commit('updateCart', this.goodsList)
  },
  computed: {
    // 是否所有的商品都勾选
    isAll: {
      get () {
        return this.goodsList.every(v => {
          return v.checked
        })
      },
      set (newValue) {
        // console.log(newValue)
        this.goodsList.forEach(v => {
          v.checked = newValue
        })
      }
    },
    badgeNum () {
      return this.goodsList.reduce((sum, item) => {
        return sum + (item.checked ? 1 : 0)
      }, 0)
    },
    totalNum () {
      // let sum = 0
      // this.goodsList.forEach(v => {
      //   sum += v.num
      // })
      // return sum
      return this.goodsList.reduce((sum, item) => {
        return sum + (item.checked ? item.num : 0)
      }, 0)
    },
    totalPrice () {
      // 每个商品的数量*价格
      return this.goodsList.reduce((sum, item) => {
        return sum + (item.checked ? (item.num * item.goods_price) : 0)
      }, 0)
    }
  },
  methods: {
		sub(item){
			item.num--
		},
		add(item){
			item.num++
		},
    doAccount () {
      // 如果商品数量为0，提示
      if (!this.totalNum) {
        this.$showToast('请添加商品')
        return
      }

      // 如果没有token，跳转登陆
      let token = wx.getStorageSync('token')
      if (!token) {
        wx.navigateTo({ url: '/pages/login/main' })
        return
      }

      // 跳转pay
      wx.navigateTo({ url: '/pages/pay/main' })
    },
    getGoodsList (cart) {
      let ids = Object.keys(cart).join(',')
      if (!ids.trim()) {
        this.goodsList = []
        this.$showToast('购物车空的！')
        return
      }
      this.$request({
        url: '/api/public/v1/goods/goodslist?goods_ids=' + ids
      }).then(data => {
        // console.log(data)

        let goodsList = data
        // cart和goodslist数据合并
        goodsList.forEach(v => {
          let obj = cart[v.goods_id]
          v.num = obj.num
          v.checked = obj.checked
        })
        this.goodsList = goodsList
      })
    }
  }
}
